import React, { useState, useEffect } from 'react';
import { Row, Spin, Card, Form, Button, Input, Table, Select, message } from 'antd';
import { SelectAjax, DatePickerString } from 'components/common';
import { requiredRule } from 'utils/validateRules';
import { getYearEducations } from 'services/yearEducationService';
import { getSemesters } from 'services/semesterService';
import { BtnAddNew } from 'components/common/button';
import { createAcademicNoteAndANDetail } from 'services/AcademicWarning';
import ModalHook from 'components/common/ModalHook';
import to from 'await-to-js';
import { getQueryParams, updateQueryParams } from 'utils/urlUtil';
import { getCodeStudent } from 'services/AddDiscipline';
import useModal from 'components/common/ModalHook/useModal';
// import TableForFormEntering from './TableForFormEntering';

export const MODAL_ID = 'ModalAddListStudentStopSchool';

const ModalAddNewStudent = ({ form, resetData, studentSelected }) => {
  const paramsSearch = getQueryParams();
  const [studentData, setStudentData] = useState({});
  const [loading, setLoading] = useState(false);
  const [, , hiddenModalUploadFileModal] = useModal('ModalAddListStudentStopSchool');
  const { getFieldDecorator, validateFields, resetFields, setFieldsValue } = form;

  useEffect(() => {
    const dataStudents = {};
    studentSelected.forEach((item, index) => {
      dataStudents[index] = { key: index, ...item };
    });
    setStudentData(dataStudents);
    resetFields();
  }, [studentSelected]);
  const handleOk = async () => {
    validateFields(async (error, values) => {
      if (error) return;
      const dataStudentAdd = Object.values(studentData).map(item => {
        return {
          studentId: item.studentId,
          courseId: item.courseId,
          classContinuteId: null,
          semesterId: values.semesterId,
          yearEduId: values.yearEduId,
        };
      });
      if (dataStudentAdd.length === 0) {
        message.error('Cha c sinh vin c chn');
        return;
      }
      const dataCreate = {
        approvalNumber: values.approvalNumber,
        approvalDate: values.approvalDate,
        approvalTypeId: values.approvalTypeId,
        description: values.description,
        anDetail: dataStudentAdd,
      };

      const [err] = await to(createAcademicNoteAndANDetail(dataCreate));
      if (err) return;
      message.success('Thm thnh cng');
      const { _time } = paramsSearch;
      const countTine = _time + 1;
      updateQueryParams({ ...paramsSearch, _time: countTine });
      resetData();
      hiddenModalUploadFileModal();
      setLoading(false);
    });
    return false;
  };

  const deleteStudent = key => {
    const dataStudent = { ...studentData };
    delete dataStudent[key];
    setStudentData(dataStudent);
  };

  const changeInputCode = async (e, index) => {
    const [error, res] = await to(getCodeStudent(e.target.value));
    if (error) return;
    const dataStudent = { ...studentData };
    dataStudent[index] = {
      key: index,
      studentCode: res.data.code,
      ...res.data,
      studentId: res.data.id,
    };
    setStudentData(dataStudent);
  };

  const columns = [
    {
      title: 'M sinh vin',
      dataIndex: 'studentCode',
      key: 'studentCode',
      render: (_text, record, index) => {
        return (
          <div>
            {record.studentCode ? (
              <div>{record.studentCode}</div>
            ) : (
              <Input onBlur={e => changeInputCode(e, index)} />
            )}
          </div>
        );
      },
    },
    {
      title: 'H v tn',
      dataIndex: 'fullname',
      key: 'fullname',
    },
    {
      title: 'Ngy sinh',
      dataIndex: 'dob',
      key: 'dob',
    },
    {
      title: 'Tn lp',
      dataIndex: 'className',
      key: 'className',
    },
    {
      title: 'Thao tc',
      dataIndex: 'operation',
      render: (text, record) => (
        <Button className="btnSmallDeleteInTable" onClick={() => deleteStudent(record.key)}>
          Xa
        </Button>
      ),
    },
  ];

  const addDataRowStudent = () => {
    const student = {
      key: Object.values(studentData).length,
      studentCode: '',
      fullname: '',
      dob: '',
      studyFieldLabel: '',
    };

    const dataStudent = { ...studentData };
    dataStudent[Object.values(studentData).length] = student;
    setStudentData(dataStudent);
  };

  const { Option } = Select;
  const { TextArea } = Input;
  const formItemLayout = {
    labelCol: {
      xs: { span: 24 },
      sm: { span: 5 },
    },
    wrapperCol: {
      xs: { span: 24 },
      sm: { span: 12 },
    },
    colon: false,
  };
  return (
    <ModalHook
      okButtonProps={{ disabled: loading }}
      cancelButtonProps={{ disabled: loading }}
      onOk={handleOk}
      width="1000px"
      height="70vh"
      title="Nhp quyt nh"
      id={MODAL_ID}
      okText="Lu"
    >
      <Spin spinning={loading}>
        <Form className="form-inline-label" layout="vertical">
          <Card title="Nhp Quyt nh thi hc, ngng hc">
            <Row>
              <Form {...formItemLayout}>
                <Form.Item {...formItemLayout} label="Nm hc" required>
                  {getFieldDecorator('yearEduId', { rules: [requiredRule] })(
                    <SelectAjax
                      service={getYearEducations}
                      setLabel={yearEdu => `${yearEdu.fromYear}-${yearEdu.toYear}`}
                      typeValue="int"
                    />
                  )}
                </Form.Item>
                <Form.Item {...formItemLayout} label="Hc k" required>
                  {getFieldDecorator('semesterId', { rules: [requiredRule] })(
                    <SelectAjax
                      setFieldsValue={setFieldsValue}
                      service={getSemesters}
                      // allowGetData={!!yearEduId}
                      // params={{ yearEduId }}
                      typeValue="int"
                    />
                  )}
                </Form.Item>
                <Form.Item {...formItemLayout} label="S Q">
                  {getFieldDecorator('approvalNumber', {
                    rules: [
                      {
                        required: true,
                        message: 'Vui lng nhp thng tin!',
                      },
                    ],
                  })(<Input />)}
                </Form.Item>
                <Form.Item {...formItemLayout} label="Ngy Q">
                  {getFieldDecorator('approvalDate', {
                    initialValue: new Date(),
                    rules: [
                      {
                        required: true,
                        message: 'Vui lng nhp thng tin!',
                      },
                    ],
                  })(<DatePickerString placeholder="Chn ngy" />)}
                </Form.Item>
                <Form.Item {...formItemLayout} label="Loi Q">
                  {getFieldDecorator('approvalTypeId', {
                    initialValue: 1,
                    rules: [
                      {
                        required: true,
                        message: 'Vui lng nhp thng tin!',
                      },
                    ],
                  })(
                    <Select>
                      <Option value={1}>Thi hc</Option>
                    </Select>
                  )}
                </Form.Item>
                <Form.Item {...formItemLayout} label="Ni dung">
                  {getFieldDecorator('description', {
                    rules: [
                      {
                        required: true,
                        message: 'Vui lng nhp thng tin!',
                      },
                    ],
                  })(<TextArea style={{ height: 100 }} />)}
                </Form.Item>
              </Form>
            </Row>
          </Card>
        </Form>
        <Row>
          <BtnAddNew onClick={addDataRowStudent} />
          <Table columns={columns} rowKey="studentCode" dataSource={Object.values(studentData)} />
        </Row>
      </Spin>
    </ModalHook>
  );
};

export default Form.create()(ModalAddNewStudent);



