import React, { useEffect, useState } from 'react';
import { stringify } from 'querystring';
import { TableData } from 'components/common';
import { Row, Col } from 'antd';
import { BtnAddNew } from 'components/common/button';
import { getStudentForcedToLeaveSchool } from 'services/AcademicWarning';
import { getQueryParams } from 'utils/urlUtil';

import useModal from 'components/common/ModalHook/useModal';
import ModalAddNewStudent from './ModalAddNewStudent';


const TableStopNotSelected = () => {
  const [paramSearch, setParamSearch] = useState({});
  const [studentData, setStudentData] = useState([]);
  const [, showModalUploadFileModal] = useModal('ModalAddListStudentStopSchool');

  useEffect(() => {
    const paramsSearchTemp = getQueryParams();
    if(stringify(paramsSearchTemp) !== stringify(paramSearch)){
      setParamSearch(paramsSearchTemp);
    }
  });

  const columns = () => [
    {
      title: 'M sinh vin',
      dataIndex: 'studentCode',
      key: 'studentCode',
    },
    {
      title: 'H v tn',
      dataIndex: 'fullname',
      key: 'fullname',
    },
    {
      title: 'Ngy sinh',
      dataIndex: 'dob',
      key: 'dob',
    },
    {
      title: 'Tn lp',
      dataIndex: 'className',
      key: 'className',
    },
    {
      title: 'TBCHK u',
      dataIndex: 'tbchK1',
      key: 'tbchK1',
    },
    {
      title: 'TBCHK 2',
      dataIndex: 'tbchK2',
      key: 'tbchK2',
    },
    {
      title: 'TBCHK 3',
      dataIndex: 'tbchK3',
      key: 'tbchK3',
    },
    {
      title: 'TBCTL',
      dataIndex: 'tbctl',
      key: 'tbctl',
    },
    {
      title: 'Nm o to',
      dataIndex: 'yearEduLabel',
      key: 'yearEduLabel',
    },
    {
      title: 'L do',
      dataIndex: 'reason',
      key: 'reason',
      render: (_text, record) => {
        return (
          <div dangerouslySetInnerHTML={{__html: record.reason}}  />
        );
      },
    },
    // {
    //   title: 'Thao tc',
    //   dataIndex: 'operation',
    //   render: () => (
    //     <Button className="btnSmallAddInTable" onClick={this.showModal}>
    //       Thm
    //     </Button>
    //   ),
    // },
  ];

  const rowSelection = {
    onChange: (selectedRowKeys, selectedRows) => {
      setStudentData(selectedRows);
    },
  };
  const { trainTypeId, facultyId, courseId, semesterId, yearEduId } = paramSearch;
  return (
    <div>
      <Row>
        <Col span={24}>
          <BtnAddNew onClick={() => showModalUploadFileModal()} />
        </Col>
        <Col span={24}>
          <TableData
            bordered
            rowSelection={rowSelection}
            columns={columns()}
            formSearch
            service={getStudentForcedToLeaveSchool}
            paramSearch={{ ...paramSearch }}
            allowGetData={!!trainTypeId && !!facultyId && !!courseId && !!semesterId && !!yearEduId}
            rowKey="studentCode"
          />
          <ModalAddNewStudent resetData={() => setStudentData([])} studentSelected={studentData} />
        </Col>
      </Row>
    </div>
  );
};

export default TableStopNotSelected;



