import { request, requestDownload } from 'utils';

const prefix = '/api/students';

export const getStudentForcedToLeaveSchool = ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  classId,
  semesterId,
  yearEduId,
}) =>
  request({
    prefix,
    url: '/AcademicWarning/GetStudentForcedToLeaveSchool',
    method: 'GET',
    params: { trainTypeId, facultyId, courseId, studyFieldId, classId, semesterId, yearEduId },
    data: {},
  });

export const getStudentInAcademicNote = ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  classId,
  semesterId,
  yearEduId,
}) =>
  request({
    prefix,
    url: '/AcademicNote/GetStudentInAcademicNote',
    method: 'GET',
    params: { trainTypeId, facultyId, courseId, studyFieldId, classId, semesterId, yearEduId },
    data: {},
  });

export const createAcademicNoteAndANDetail = dataCreate =>
  request({
    prefix,
    url: '/AcademicNote/CreateAcademicNoteAndANDetail',
    method: 'POST',
    data: { ...dataCreate },
  });

export const exportExcelStudenForcedToLeaveSchool = ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  classId,
  semesterId,
  yearEduId,
}) =>
  requestDownload({
    prefix,
    defaultFileName: 'exportExcelStudenForcedToLeaveSchool.xlsx',
    url: '/AcademicWarning/ExportExcelStudenForcedToLeaveSchool',
    method: 'POST',
    data: { trainTypeId, facultyId, courseId, studyFieldId, classId, semesterId, yearEduId },
  });

