import React, { useState, useEffect } from 'react';
import { Row, Col, Card, Divider, Spin } from 'antd';
import { stringify } from 'querystring';
import { BtnExport } from 'components/common/button';
import { CartHeadAction } from 'components/common';
import { exportExcelStudenForcedToLeaveSchool } from 'services/AcademicWarning';
import { getQueryParams } from 'utils/urlUtil';
import TableForcedStudySelected from './TableForcedStudySelected';
import TableForcedStudy from './TableForcedStudy';

const TabStopSchool = () => {
  const [loading, setLoading] = useState(false);
  const [paramSearch, setParamSearch] = useState({});

  useEffect(() => {
    const paramsSearchTemp = getQueryParams();
    if(stringify(paramsSearchTemp) !== stringify(paramSearch)){
      setParamSearch(paramsSearchTemp);
    }
  });

  const exportExcel = async () => {
    setLoading(true);
    try {
      await exportExcelStudenForcedToLeaveSchool(paramSearch);
    } catch (error) {
        console.log('error');
    } finally {
      setLoading(false);
    }
    
  };

  return (
    <Card>
      <Spin spinning={loading}>
        <Divider>Danh sch sinh vin</Divider>
        <Row>
          <Col span={24}>
            <TableForcedStudy />
          </Col>
        </Row>
        <Divider>Danh sch sinh vin  c quyt nh</Divider>
        <CartHeadAction>
          <BtnExport onClick={exportExcel} />
        </CartHeadAction>
        <Row className="rowMargin">
          <Col span={24}>
            <TableForcedStudySelected />
          </Col>
        </Row>
      </Spin>
    </Card>
  );
};

export default TabStopSchool;



