import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col } from 'antd';
import FormInfoCommon from './FormInfoCommon';
// import MenuTabTop from './MenuTabTop';
import './style.scss';

class WForcedStopSchoolPage extends Component {
  state = {};
  render() {
    return (
      <Card title="Buc thi hc ">
        <Row>
          <Col span={24}>
            <FormInfoCommon />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <WForcedStopSchoolPage />
  </Page>
);



