import React, { Component } from 'react';
import { stringify } from 'querystring';
import { TableData } from 'components/common';
import { getStudentInAcademicNote } from 'services/AcademicWarning';
import { getQueryParams } from 'utils/urlUtil';

class TableAcademicWarning extends Component {
  constructor(props) {
    super(props);
    this.state = {
      paramSearch: {},
    };

    this.columns = [
      {
        title: 'M sinh vin',
        dataIndex: 'studentCode',
        key: 'studentCode',
      },
      {
        title: 'H v tn',
        dataIndex: 'fullname',
        key: 'fullname',
      },
      {
        title: 'Ngy sinh',
        dataIndex: 'dob',
        key: 'dob',
      },
      {
        title: 'Tn lp',
        dataIndex: 'className',
        key: 'className',
      },
      {
        title: 'TBCHK u',
        dataIndex: 'tbchK1',
        key: 'tbchK1',
      },
      {
        title: 'TBCHK 2',
        dataIndex: 'tbchK2',
        key: 'tbchK2',
      },
      {
        title: 'TBCHK 3',
        dataIndex: 'tbchK3',
        key: 'tbchK3',
      },
      {
        title: 'TBCTL',
        dataIndex: 'tbctl',
        key: 'tbctl',
      },
      {
        title: 'Nm o to',
        dataIndex: 'yearEduLabel',
        key: 'yearEduLabel',
      },
      {
        title: 'Ni dung',
        dataIndex: 'description',
        key: 'description',
      },
      {
        title: 'L do',
        dataIndex: 'reason',
        key: 'reason',
        render: (_text, record) => {
          return (
            <div dangerouslySetInnerHTML={{__html: record.reason}}  />
          );
        },
      },
    ];
  }
  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  handleChangeQueryParams = () => {
    const { paramSearch } = this.state;
    const paramsSearchTemp = getQueryParams();
    if(stringify(paramsSearchTemp) !== stringify(paramSearch)){
      this.setState({ paramSearch: paramsSearchTemp });
    }
  };

  render() {
    const { paramSearch } = this.state;
    const { trainTypeId, facultyId, courseId, semesterId, yearEduId } = paramSearch;
    return (
      <div>
        <TableData
          bordered
          columns={this.columns}
          formSearch
          service={getStudentInAcademicNote}
          paramSearch={paramSearch}
          allowGetData={!!trainTypeId && !!facultyId && !!courseId && !!semesterId && !!yearEduId}
          rowKey="id"
        />
      </div>
    );
  }
}

export default TableAcademicWarning;



